:root {
  --ink-900: #1d1a12;
  --ink-700: #3a3527;
  --ink-500: #5a5240;
  --sand-50: #fbf6ea;
  --sand-100: #f2e6c8;
  --field-300: #c2d59a;
  --field-500: #9fc27b;
  --accent-500: #ffb65c;
  --accent-700: #ff9a33;
  --panel-bg: rgba(24, 23, 18, 0.88);
  --panel-border: rgba(255, 255, 255, 0.08);
  --panel-glow: rgba(255, 182, 92, 0.2);
  --radius-lg: 24px;
  --radius-md: 16px;
  --shadow-soft: 0 24px 60px rgba(18, 16, 12, 0.35);
  --shadow-tight: 0 10px 30px rgba(18, 16, 12, 0.3);
  --font-sans: var(--font-space-grotesk), 'Space Grotesk', system-ui, -apple-system,
    BlinkMacSystemFont, 'Segoe UI', sans-serif;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html,
body {
  height: 100%;
}

body {
  color: var(--sand-50);
  background: radial-gradient(circle at top, #f9f1d8 0%, #d7e7bb 40%, #b9d08f 100%);
  font-family: var(--font-sans);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  -webkit-tap-highlight-color: transparent;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  font-family: inherit;
}

canvas:focus-visible {
  outline: 2px solid var(--accent-500);
  outline-offset: -6px;
}

.log-panel {
  position: absolute;
  top: calc(24px + env(safe-area-inset-top));
  right: calc(24px + env(safe-area-inset-right));
  width: min(420px, 92vw);
  max-height: calc(
    100vh - 48px - env(safe-area-inset-top) - env(safe-area-inset-bottom)
  );
  overflow: hidden;
  background: var(--panel-bg);
  border: 1px solid var(--panel-border);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-soft);
  backdrop-filter: blur(12px);
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 24px;
  z-index: 10;
}

.log-panel__header {
  display: flex;
  justify-content: space-between;
  gap: 24px;
}

.log-panel__header > div,
.log-panel__item-head > * {
  min-width: 0;
}

.log-panel__eyebrow {
  font-size: 12px;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.6);
}

.log-panel__title {
  font-size: 28px;
  margin: 4px 0 6px;
  text-wrap: balance;
}

.log-panel__meta {
  font-size: 13px;
  color: rgba(255, 255, 255, 0.7);
}

.log-panel__stats {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 12px;
  text-align: right;
}

.log-panel__stat-label {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.55);
}

.log-panel__stat-value {
  display: block;
  font-size: 22px;
  margin-top: 4px;
  color: var(--accent-500);
  font-variant-numeric: tabular-nums;
}

.log-panel__section {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.log-panel__section-head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
}

.log-panel__section h2 {
  font-size: 16px;
  text-wrap: balance;
}

.log-panel__chip {
  font-size: 12px;
  padding: 4px 10px;
  border-radius: 999px;
  background: rgba(255, 255, 255, 0.08);
}

.log-panel__detail {
  display: grid;
  gap: 8px;
  font-size: 13px;
  color: rgba(255, 255, 255, 0.7);
}

.log-panel__detail strong {
  display: block;
  color: var(--sand-50);
  font-weight: 500;
  margin-top: 4px;
  overflow-wrap: anywhere;
}

.log-panel__actions {
  display: flex;
  gap: 12px;
}

.log-panel__hint {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.55);
}

.log-panel__button {
  flex: 1;
  padding: 10px 14px;
  border-radius: 999px;
  border: none;
  background: linear-gradient(120deg, var(--accent-500), var(--accent-700));
  color: #2c1c08;
  font-weight: 600;
  cursor: pointer;
  transition: transform 0.15s ease;
  box-shadow: var(--shadow-tight);
  touch-action: manipulation;
}

.log-panel__button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  box-shadow: none;
}

.log-panel__button--ghost {
  background: transparent;
  color: var(--sand-50);
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: none;
}

.log-panel__button:not(:disabled):hover,
.log-panel__button:not(:disabled):focus-visible {
  transform: translateY(-1px);
  box-shadow: 0 12px 26px rgba(255, 164, 77, 0.35);
}

.log-panel__section--logs {
  flex: 1;
  min-height: 0;
}

.log-panel__list {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 12px;
  max-height: 360px;
  overflow-y: auto;
  padding-right: 4px;
  overscroll-behavior: contain;
}

.log-panel__item {
  padding: 12px;
  border-radius: var(--radius-md);
  background: rgba(12, 12, 10, 0.55);
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.log-panel__item-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
  font-size: 12px;
  color: rgba(255, 255, 255, 0.7);
}

.log-panel__time {
  font-variant-numeric: tabular-nums;
}

.log-panel__role {
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 0.08em;
  color: var(--accent-500);
}

.log-panel__role--assistant {
  color: #9fd3ff;
}

.log-panel__role--system {
  color: #f6d38f;
}

.log-panel__role--tool {
  color: #b7f6b2;
}

.log-panel__text {
  font-size: 13px;
  color: rgba(255, 255, 255, 0.86);
  line-height: 1.5;
  overflow-wrap: anywhere;
}

.log-panel__session {
  display: inline-block;
  margin-top: 8px;
  font-size: 11px;
  color: rgba(255, 255, 255, 0.5);
  overflow-wrap: anywhere;
}

.log-panel__empty {
  font-size: 13px;
  color: rgba(255, 255, 255, 0.6);
}

@media (max-width: 900px) {
  .log-panel {
    position: static;
    width: 100%;
    max-height: none;
    border-radius: 0;
  }
}

@media (prefers-reduced-motion: reduce) {
  .log-panel__button {
    transition: none;
  }
}
